<template>
    <s-header :name="''"></s-header>
    <div style="background-color: #F2F4F8;overflow-y: scroll;height: calc(100vh - 99px)">
        <!---->
        <div class="item" v-for="item in defalutLists" :key="item.id">
            <div style="display: flex;align-items: center;flex: 2;">
                <svg aria-hidden="true" style="width: 14px;justify-content: center;align-items: center"
                     v-if="!isback"
                     @click="goBack">
                    <use :xlink:href="item.icon"></use>
                </svg>
                <p class="title">{{item.text}}</p>
            </div>
        </div>
        <draggable @change="log" :list="expandLists">
            <div class="item" v-for="item in expandLists" :key="item.id">
                <!--item-->
                <div style="display: flex;align-items: center;flex: 2;justify-content: space-between"
                >
                    <div style="display: flex;align-items: center">
                        <img style="width: 25px;" src="@/assets/ic_left.png" alt=""/>
                        <p class="title">{{item.text}}</p>
                    </div>
                    <img style="width: 19px;" src="@/assets/ic_close.png" alt=""/>
                </div>
            </div>
        </draggable>
        <!---->
        <div class="item" :style="index==0&&'margin-top: 10px'" v-for="(item,index) in expandLists" :key="item.id"
             @click="handleChangeStatus(item, true)">
            <div style="display: flex;align-items: center;flex: 2;justify-content: space-between">
                <div style="display: flex;align-items: center">
                    <div style="width: 25px;"/>
                    <p class="title">{{item.text}}</p>
                </div>
                <img style="width: 24px;" src="@/assets/ic_add.png" alt=""/>
            </div>
        </div>
        <div class="bottom_des">
            
        </div>
    </div>
</template>

<script>
  import sHeader from '@/components/SimpleHeader'
  import {reactive, computed, onMounted,} from 'vue'
  import {useRouter} from 'vue-router'
  import {VueDraggableNext} from 'vue-draggable-next'

  export default {
    name: "index",
    components: {
      sHeader,
      draggable: VueDraggableNext,
    },
    setup() {
      const router = useRouter()

      onMounted(
        () => {

        }
      )

      // reactive Vue2.0Vue.observer
      const state = reactive({
        defalutList: [{
          id: 1,
          done: false,
          text: '',
          icon: '#icon-heshui',
        }, {
          id: 2,
          done: false,
          text: '',
          icon: '#icon-_shanshijiankang',

        }, {
          id: 3,
          done: false,
          text: '',
          icon: '#icon-shuimian',
        }, {
          id: 4,
          done: false,
          text: '',
          icon: '#icon-paobu',
        },
        ],
        expanddata: [
          {
            id: 5,
            done: false,
            text: '',
            icon: '#icon-xueya',
          }, {
            id: 6,
            done: false,
            text: '',
            icon: '#icon-xinlv',
          }, {
            id: 7,
            done: false,
            text: '',
            icon: '#icon-xuetang',
          }, {
            id: 8,
            done: false,
            text: '/',
            icon: '#icon-chengtizhong',
          },
        ],

        message: [
          "vue.draggable",
          "draggable",
          "component",
          "for",
          "vue.js 2.0",
          "based",
          "on",
          "Sortablejs"
        ],
        todo: ''
      })
      // 
      const defalutLists = computed(() => {
        return state.defalutList.filter(item => !item.done)
      })
      const expandLists = computed(() => {
        return state.expanddata.filter(item => !item.done)
      })

      const rightclick = () => {
        router.push({path: 'EditCard'})
      }

      const log = (event) => {
        console.log(event.moved)
        const old = state.expanddata[event.moved.oldIndex];
        state.expanddata[event.moved.oldIndex] = state.expanddata[event.moved.newIndex];
        state.expanddata[event.moved.newIndex] = old;

      }
      // Vue3.0setup  return template
      return {
        state,
        defalutLists,
        expandLists,
        rightclick,
        log,
      }
    }
  }
</script>

